import * as React from 'react';
import { Router as ReachRouter } from '@reach/router';
import UserPageLayout from '../../../common/components/layout/UserPageLayout';
import Routes, { RoutePaths } from '../../../common/routing/Routes';
import GuestAuth from '../containers/GuestAuth';
import Redirect from './Redirect';

const GuestRouter = (props: any) => (
  <UserPageLayout
    title="Min olio"
    page={Routes.Dashboard()}
    showFooterMenu
    hasNavbar
    hasLogin
    {...props}>
    <ReachRouter>
      <GuestAuth path={RoutePaths.GuestAuthentication}>
        <Redirect to={Routes.Bookings()} default />
      </GuestAuth>
    </ReachRouter>
  </UserPageLayout>
);

export default GuestRouter;
