import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { RouteComponentProps } from '@reach/router';
import { RootState } from '../../../root.reducer';
import { userdataIsLoading, isLoggedIn } from '../forces/selectors';
import { loginGuest } from '../forces/actions';
import LoadingIndicator from '../../../common/components/loadingIndicator/LoadingIndicator';

interface RouterProps {
  userId: string;
}
interface OwnProps {}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps &
  StateProps &
  OwnProps &
  React.HTMLAttributes<HTMLElement> &
  RouteComponentProps<RouterProps>;

class GuestAuth extends React.Component<Props> {
  componentDidMount() {
    if (!this.props.isLoggedIn && this.props.userId) {
      this.props.loginGuest(this.props.userId);
    }
  }

  render() {
    return <LoadingIndicator />;
  }
}

const mapStateToProps = (state: RootState) => ({
  isUserdataIsLoading: userdataIsLoading(state),
  isLoggedIn: isLoggedIn(state),
});
const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      loginGuest,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(GuestAuth);
