import React from 'react';
import SEO from '../seo';
import classNames from 'classnames';
import Footer from '../footer/Footer';
import favicon from '../../../assets/images/favicon.png';
import Logo from '../logo/Logo';
import SectionTitle from '../section/SectionTitle';
import SectionSubtitle from '../section/SectionSubtitle';
import Link from '../link/Link';
import Routes from '../../routing/Routes';
import BodyBolderText from '../texts/BodyBolderText';
import { Match } from '@reach/router';
import CallToActionLink from '../callToAction/CallToActionLink';
import BookMenu from '../bookMenu/BookMenu';
import styles from './UserPageLayout.module.css';
import PrivacyPolicy from '../privacyPolicy/PrivacyPolicy';
import InfoMessage from '../infoMessage/InfoMessage';
import config from '../../../config';

interface Tab {
  route: string;
  title: string;
}

const renderTabs = (tabs: Array<Tab>) => {
  if (tabs.length === 0) {
    return;
  }
  return (
    <div className={styles.navbar}>
      {tabs.map((page, index) => (
        <Match key={index} path={`${page.route}/*`}>
          {props => {
            const isActive = props.match != null;
            return (
              <Link to={page.route}>
                <SectionSubtitle
                  className={classNames(styles.text, {
                    [styles.textSelected]: isActive,
                  })}
                  data-title={page.title}>
                  {page.title}
                </SectionSubtitle>
              </Link>
            );
          }}
        </Match>
      ))}
    </div>
  );
};

const renderFooterMenu = (showBookButton: boolean, showLogoutButton: boolean) => (
  <div className={styles.footerMenuContainer}>
    {showBookButton && <CallToActionLink to={Routes.Booking()}>Book time</CallToActionLink>}
    {showLogoutButton && (
      <Link underlined type="secondary" to={Routes.Goodbye()}>
        Logg ut
      </Link>
    )}
  </div>
);

type Props = {
  showBookButton?: boolean;
  showLogoutButton?: boolean;
  title: string;
  tabs?: Array<Tab>;
} & React.HTMLAttributes<HTMLElement>;

const UserPageLayout: React.FunctionComponent<Props> = ({
  title,
  showBookButton = false,
  showLogoutButton = false,
  children,
  className,
  tabs = [],
}) => (
  <div>
    <div>
      <SEO
        title="olio"
        link={[{ rel: 'shortcut icon', type: 'image/x-icon', href: `${favicon}` }]}
      />
      <div className={styles.menuContainer}>
        <BookMenu />
      </div>
      <div className={classNames(styles.container, className)}>
        <Link to={Routes.Main()} className={styles.backButton}>
          <BodyBolderText>Hjem</BodyBolderText>
        </Link>
        <div className={classNames(styles.innercontainer, className)}>
          <div className={styles.sectionTitle}>
            <SectionTitle>{title}</SectionTitle>
            {renderTabs(tabs)}
          </div>
          {children}
          {renderFooterMenu(showBookButton, showLogoutButton)}
        </div>
      </div>
      <Footer />
      <div className={styles.infoContainer}>
        {config.bookingDisabled && <InfoMessage />}
        <PrivacyPolicy />
      </div>
      <PrivacyPolicy />
    </div>
  </div>
);

export default UserPageLayout;
